import '../Css/bootstrap.min.css';
import '../Css/style.css';
import Nav from './Nav';
import SepreratorSpace from './SepreratorSpace';
import Footer from './Footer';
import React from 'react';
function Body() {
    return (
        <React.Fragment>
            <div className='container-xxl py-lg-5 py-md-3 mb-4'>
                <div className='container'>
                    <div className='row g-5'>
                        <div className='col-lg-6 wow fadeInUp' data-wow-delay='0.1s'>
                            <div className='position-relative overflow-hidden h-100' style={{ minHeighteight: "400px" }}>
                                <img className='position-absolute w-100 h-100 pt-5 pe-5' src='img/LOGO.svg' alt='' style={{ objectFit: "cover"}} />
                                {/* <img className='position-absolute top-0 end-0 bg-white ps-2 pb-2' src='img/about-2.jpg' alt='' style={{ width: "200px", height: "200px" }} /> */}
                            </div>
                        </div>
                        <div className='col-lg-6 wow fadeInUp' data-wow-delay='0.5s'>
                            <div className='h-100'>
                                <div className='d-inline-block rounded-pill bg-secondary text-primary py-1 px-3 mb-3'>About Us</div>
                                <h1 className='display-6 mb-5'>
                                    We help people share "Curriculum Vitae" with each other
                                </h1>
                                <div className='bg-light border-bottom border-5 border-primary rounded p-4 mb-4'>
                                    <p className='text-dark mb-2'>This is just a personal project</p>
                                    <span className='text-primary'>Vo Duong, Coder</span>
                                </div>
                                <p className='mb-5'>I will deploy the site within 1 year for testing - hope everyone supports Hosting and Domain costs if you want to maintain the website longer.</p>
                                <a className='btn btn-primary py-2 px-3 me-3' href=''>
                                    Donate
                                    <div className='d-inline-flex btn-sm-square bg-white text-primary rounded-circle ms-2'>
                                        <i className='fa fa-money-bill'></i>
                                    </div>
                                </a>
                                <a className='btn btn-outline-primary py-2 px-3' href=''>
                                    Chat with me
                                    <div className='d-inline-flex btn-sm-square bg-primary text-white rounded-circle ms-2'>
                                        <i className='fa fa-comment'></i>
                                    </div>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
}
function About({ data,p,u }) {
    return (
        <React.Fragment>
            <Nav appInfor={data} a={p} u={u} />
            <SepreratorSpace />
            <Body />
            <Footer appInfor={data} />
        </React.Fragment>
    );
}

export default About;
