import '../Css/bootstrap.min.css';
import '../Css/style.css';
import '../Css/color.css';
import 'bootstrap'
import Nav from './Nav';
import SepreratorSpace from './SepreratorSpace';
import Footer from './Footer';
import Template from './Template';
import React from 'react';
import {
    BrowserRouter as Router, Route, Routes, Link,
    useSearchParams
} from 'react-router-dom';
function TabTemplate() {
    return (
        <div className='row mx-2 g-3 justify-content-center'>
            <ChildrenTemplate />
        </div>
    );
}
function ChildrenTemplate() {
    return (

        <div className='col-lg-4 col-md-6 wow fadeInUp mb-0' data-wow-delay='0.1s'>
            <div className='causes-item d-flex flex-column bg-white rounded-3 overflow-hidden mt-3' style={{ boxShadow: '0px 0px 10px #001D23' }}>
                <a href='/CV-Template?cv=123'>
                    <div className='text-left p-0'>
                        <div className='position-relative'>
                            <img className='img-fluid' src='img/courses-1.jpg' alt='' />
                            <div className='causes-overlay'>
                                <div className='btn btn-outline-primary'>
                                    Use
                                    <div className='d-inline-flex btn-sm-square bg-primary text-white rounded-circle ms-2'>
                                        <i className='fa fa-arrow-right'></i>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className='d-inline-block text-dark fw-bold fs-5 p-2 mb-0 col-8 border-top'>
                            <small>CV VIP 01</small>
                        </div>
                        <div className='d-inline-block text-dark fw-bold fs-5 p-2 mb-0 col-4 text-center border-top'>
                            <i className="mx-1 fa fa-circle t-red"></i>
                            <i className="mx-1 fa fa-circle t-blue"></i>
                            <i className="mx-1 fa fa-circle t-green"></i>
                        </div>
                    </div>
                </a>
            </div>
        </div>
    )
}
function Body() {
    return (
        <React.Fragment>
            <div className='container-xxl text-secondary bg-white shadow '>
                <div className='container'>
                    <div className="tab-pane fade show active pt-4"><div className='text-center mx-auto mb-5 wow fadeInUp' data-wow-delay='0.1s' style={{ maxWidth: "500px" }}>
                        <div className='d-inline-block rounded-pill bg-secondary text-primary py-1 px-3 mb-3'>Curriculum Vitae</div>
                        <h1 className='display-6 mb-5'>Using Template or Upload Curriculum Vitae</h1>
                    </div>
                    </div>
                    <div className="d-flex row align-items-start">
                        <div className="nav flex-column nav-pills col-12 col-lg-1" id="v-pills-tab" role="tablist" aria-orientation="vertical">
                            <button className="nav-link rounded-0 active" id="v-pills-home-tab" data-bs-toggle="pill" data-bs-target="#v-pills-home" type="button" role="tab" aria-controls="v-pills-home" aria-selected="true">Template</button>
                            <button className="nav-link rounded-0" id="v-pills-profile-tab" data-bs-toggle="pill" data-bs-target="#v-pills-profile" type="button" role="tab" aria-controls="v-pills-profile" aria-selected="false">Your CV</button>
                            <button className="nav-link rounded-0" id="v-pills-messages-tab" data-bs-toggle="pill" data-bs-target="#v-pills-messages" type="button" role="tab" aria-controls="v-pills-messages" aria-selected="false">Edit Information</button>
                            <button className="nav-link rounded-0" id="v-pills-settings-tab" data-bs-toggle="pill" data-bs-target="#v-pills-settings" type="button" role="tab" aria-controls="v-pills-settings" aria-selected="false">Settings</button>
                        </div>
                        <div className=" tab-content col-12 col-lg-11 mb-4" id="v-pills-tabContent">
                            <div className="tab-pane fade show active" id="v-pills-home" role="tabpanel" aria-labelledby="v-pills-home-tab" tabIndex="0"><TabTemplate /></div>
                            <div className="tab-pane fade" id="v-pills-profile" role="tabpanel" aria-labelledby="v-pills-profile-tab" tabIndex="0">haha</div>
                            <div className="tab-pane fade" id="v-pills-messages" role="tabpanel" aria-labelledby="v-pills-messages-tab" tabIndex="0"><TabTemplate /></div>
                            <div className="tab-pane fade" id="v-pills-settings" role="tabpanel" aria-labelledby="v-pills-settings-tab" tabIndex="0">...</div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment >
    );
}
function CV({ data, p, u, isSignIn }) {
    const [searchParams] = useSearchParams();
    const key = searchParams.get('cv');
    if (isSignIn) {
        if (key != null && key != '') {
            return (
                <React.Fragment>
                    <div id='rx'>
                        <Nav appInfor={data} a={p} u={u} />
                        <SepreratorSpace />
                    </div>
                    <div id='bodyform'>
                        <Template />
                    </div>
                    <div id='rx'>
                        <Footer appInfor={data} />
                    </div>
                </React.Fragment>
            );

        }
        else {
            return (
                <React.Fragment>
                    <Nav appInfor={data} a={p} u={u} />
                    <SepreratorSpace />
                    <Body />
                    <Footer appInfor={data} />
                </React.Fragment>
            );
        }

    }
    else {
        window.location.href = '/Sign-In';
    }
}

export default CV;
