import React from 'react';

const NotFound = () => (
    <div className="container-fluid wow fadeInUp" data-wow-delay="0.1s">
        <div className="col-12 col-lg-6 text-center bg-white my-5 py-5 m-auto">
            <div className="row m-auto">
                <div className="col-lg-6 m-auto">
                    <i className="bi bi-exclamation-triangle display-1 text-primary"></i>
                    <h1 className="display-1">404</h1>
                    <h1 className="mb-4">Page Not Found</h1>
                    <p className="mb-4">Were sorry, the page you have looked for does not exist in our website! Maybe go to our home page or try to use a search?</p>
                    <a className="btn btn-outline-primary py-2 px-3" href='/'>
                        Go Back To Home
                        <div className="d-inline-flex btn-sm-square bg-primary text-white rounded-circle ms-2">
                            <i className="fa fa-arrow-right"></i>
                        </div>
                    </a>
                </div>
            </div>
        </div>
    </div>
);

export default NotFound;