import '../Css/bootstrap.min.css';
import '../Css/style.css';
import '../Css/color.css';
import 'bootstrap'
import Nav from './Nav';
import SepreratorSpace from './SepreratorSpace';
import Footer from './Footer';
import React,{useEffect} from 'react';
import {gapi} from 'gapi-script'
import LoginButton from './LoginButton';
import $ from 'jquery';

function SignIn() {
    const client_id = '191306127159-racdjj9b16jg28fiose49bipgs49t57p.apps.googleusercontent.com' 
    useEffect(()=>{
        function start(){
            gapi.client.init({
                clienId:client_id,
                scope: ""
            })
        };
        gapi.load('client:auth2',start)
    })
    return (
        <div className='container-xxl py-lg-5 py-md-3 mb-4'>
            <div className='container'>
                <div className='col-12 col-md-5 p-4 m-auto  bg-warnin'>
                    <div className='m-auto g-5'>
                        <h3>ng Nhp</h3>
                    </div>
                    <div className='m-auto g-5 col-11 col-lg-10 text-dark'>
                        <input className='form-input my-3 p-2 col-12' id='username' placeholder='Username' />
                        <input className='form-input my-3 p-2 col-12' id='password' placeholder='Password' />
                        <input className='form-check-input p-2 col-12' type='checkbox' id='remember' /> remember ?
                        <button type="button" className='btn btn-dark my-3 p-x col-12 d-block m-auto'> Login</button>
                        <div style={{ width: '100%', height: '13px', borderBottom: '1px solid #00283059', textAlign: 'center' }}>
                            <span style={{ color: '#00283059', backgroundColor: 'white', padding: '0 10px' }}>
                                Or
                                Login As
                            </span>
                        </div>
                        <div className='google my-2' id='google'>
                            <LoginButton/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
function SignUp() {
    function track(){
        if($('#licence:checked').val()=='on')
            $(".registerbtn").removeClass("disabled");
        else
            $(".registerbtn").addClass("disabled");
    }
    return (
        <React.Fragment>
            <div className='container-xxl py-lg-5 py-md-3 mb-4'>
            <div className='container'>
                <div className='col-12 col-md-5 p-4 m-auto  bg-warnin'>
                    <div className='m-auto g-5'>
                        <h3>SignUp</h3>
                    </div>
                    <div className='m-auto g-5 col-11 col-lg-10 text-dark'>
                        <input className='form-input my-3 p-2 col-12' id='username' placeholder='Username' />
                        <input className='form-input my-3 p-2 col-12' id='password' type={'password'} placeholder='Password' />
                        <input className='form-input my-3 p-2 col-12' id='password2' type={'password'} placeholder='Confirm password' />
                        <input className='form-check-input p-2 col-12' type='checkbox' id='licence' onChange={track} /> <a href='/licence' className='t-blue'>By signing up you agree to our terms of service</a>
                        <button type="button" className='btn btn-dark my-3 p-x col-12 d-block m-auto disabled registerbtn'> Register</button>
                        <div style={{ width: '100%', height: '13px', borderBottom: '1px solid #00283059', textAlign: 'center' }}>
                            <span style={{ color: '#00283059', backgroundColor: 'white', padding: '0 10px' }}>
                                Or
                                Login As
                            </span>
                        </div>
                        <div className='google my-2' id='google'>
                            <LoginButton/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        </React.Fragment >
    );
}

function Auth({ isSignIn, data, p, link,u }) {
    if (isSignIn) {
        window.location.href = "/";
    }
    else {
        return (<React.Fragment>
            {link == 'Sign-Up' ? <Nav appInfor={data} a={p} u={u} /> : <Nav appInfor={data} a={p} u={u} />}
            <SepreratorSpace />
            {link == 'Sign-Up' ? <SignUp /> : <SignIn />}
            <Footer appInfor={data} />
        </React.Fragment>)

    }
}

export default Auth;
