import '../Css/bootstrap.min.css';
import '../Css/style.css';
import '../Css/color.css';
import React from "react";
function Nav({ appInfor, u }) {
    return (
        <React.Fragment>
            <div id='spinner' className='show bg-dark position-fixed translate-middle w-100 vh-100 top-50 start-50 d-flex align-items-center justify-content-center'>
                <div className='spinner-grow text-primary' role='status'></div>
            </div>
            <div className='container-fluid fixed-top ft1 px-0 wow fadeIn' data-wow-delay='0.1s'>
                <div className='top-bar text-white-50 row gx-0 align-items-center d-none d-lg-flex'>
                    <div className='col-lg-6 px-5 text-start'>
                        <small className='t-white'><i className='fa fa-map-marker-alt text-primary me-2'></i>{appInfor.address}</small>
                        <small className='ms-4 t-white'><i className='fa fa-envelope me-2 text-primary'></i>{appInfor.email}</small>
                    </div>
                    <div className='col-lg-6 px-5 text-end'>
                        <small className='t-white'>Follow us:</small>
                        <a className='text-white-50 ms-3' href={appInfor.facebook}><i className='fab fa-facebook-f'></i></a>
                        <a className='text-white-50 ms-3' href={appInfor.twitter}><i className='fab fa-twitter'></i></a>
                        <a className='text-white-50 ms-3' href={appInfor.linkdln}><i className='fab fa-linkedin-in'></i></a>
                        <a className='text-white-50 ms-3' href={appInfor.telegram}><i className='fab fa-telegram-plane'></i></a>
                    </div>
                </div>

                <nav className='navbar navbar-expand-lg navbar-dark py-lg-0 px-lg-5 wow fadeIn' data-wow-delay='0.1s'>
                    <a href='/' className='navbar-brand ms-4 ms-lg-0'>
                        <h1 className='fw-bold text-primary m-0'>{appInfor.name1}<span className='text-white'>{appInfor.name2}</span></h1>
                    </a>
                    <button type='button' className='navbar-toggler me-4' data-bs-toggle='collapse' data-bs-target='#navbarCollapse'>
                        <span className='navbar-toggler-icon'></span>
                    </button>
                    <div className='collapse navbar-collapse' id='navbarCollapse'>
                        <div className='navbar-nav ms-auto p-4 p-lg-0 bd'>
                            <a href='/' className='nav-item nav-link active '>Home</a>
                            <a href='/about' className='nav-item nav-link '>About</a>
                            <a href='/CV-Template' className='nav-item nav-link '>CV</a>
                            <div className='nav-item dropdown'>
                                <a href='#' className='nav-item nav-link  dropdown-toggle' data-bs-toggle='dropdown'>{u.text != '' ? u.text : 'Sign-In'}</a>
                                {u.body}
                            </div>
                        </div>
                    </div>
                </nav>
            </div>
        </React.Fragment>
    );
}

export default Nav;
