import '../Css/bootstrap.min.css';
import '../Css/style.css';

import React from 'react';
function Carousel(img) {
    return (
        <React.Fragment>
            <div className='container-fluid p-0 mb-0'>
                <div id='header-carousel' className='carousel slide' data-bs-ride='carousel'>
                    <div className='carousel-inner'>
                        <div className='carousel-item active'>
                            <img className='w-100' src={'img/'+img.img} alt='Image' />
                            <div className='carousel-caption'>
                                <div className='container'>
                                    <div className='row justify-content-center'>
                                        <div className='col-lg-7 pt-0'>
                                            <h1 className='display-4 text-white mb-3 animated slideInDown'>Upload Your CV</h1>
                                            <p className='fs-5 text-white-50 mb-5 animated slideInDown'>Create a link to your CV and make something</p>
                                            <a className='btn btn-primary py-2 px-3 animated slideInDown' href='/CV-Template'>
                                                Upload
                                                <div className='d-inline-flex btn-sm-square bg-white text-primary rounded-circle ms-2'>
                                                    <i className='fa fa-arrow-up'></i>
                                                </div>
                                            </a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
}

export default Carousel;
