import React, { useState, useEffect } from "react";
import {
  BrowserRouter as Router, Route, Routes
} from 'react-router-dom';
import boostrap from 'bootstrap';
import axios from 'axios';
import NavIndex from './Component/NavIndex';
import CarouselIndex from './Component/CarouselIndex';
import About from './Component/About';
import CV from './Component/CV';
import Auth from './Component/Auth';
import NotFound from './NotFound';
import './js/main.js';
import Footer from './Component/Footer';
import AppInfor from "./API/appInfor";
function App() {
  var appInfor;
  var sign = false;
  const IconU = (<i className="fa fa-user fs-5"></i>);
  var user;
  const GetDataAppInfor = data => {
    appInfor = data;
  }
  AppInfor(GetDataAppInfor);
  if (sign) {
    user = {
      text: IconU,
      link: '/User',
      body: (<div className='dropdown-menu p-2 m-0 overflow-hidden'>
        <a href='service.html' className='dropdown-item '>Edit Profile</a>
        <a href='donate.html' className='dropdown-item '>Donate</a>
        <a href='team.html' className='dropdown-item '>Change Password</a>
        <a href='/Sign-Out' className='dropdown-item '>Logout</a>
      </div>)
    };
  }
  else {
    user = {
      text: 'Login',
      link: '/Sign-In',
      body: (<div className='dropdown-menu p-2 m-0 overflow-hidden'>
        <a href='/Sign-In' className='dropdown-item '>Sign-In</a>
        <a href='/Sign-Up' className='dropdown-item '>Sign-Up</a>
        <a href='/Donate' className='dropdown-item '>Donate</a>
      </div>)
    };
  }
  return (
    <Router>
      <Routes>
        <Route exact path='/'
          element={<React.Fragment>
            <NavIndex appInfor={appInfor} u={user} />
            <CarouselIndex img={'c1.jpg'} />
            <Footer appInfor={appInfor} />
          </React.Fragment>}
        />
        <Route path='/about' element={<About data={appInfor} p={'About'} u={user} />} />
        <Route path='/CV-Template' element={< CV data={appInfor} p={'CV'} isSignIn={sign} u={user} />} />
        <Route path='/Sign-In' element={<Auth data={appInfor} isSignIn={sign} p={'User'} u={user} link='Sign-In' />} />
        <Route path='/Sign-Up' element={<Auth data={appInfor} isSignIn={sign} p={'User'} u={user} link='Sign-Up' />} />
        <Route path='*' element={<NotFound />} />
      </Routes>
    </Router>
  );
}
export default App;
