import '../Css/bootstrap.min.css';
import '../Css/style.css';
import React from "react";
import $ from 'jquery';
function Nav({ appInfor, a,u }) {
    if(a!='')
        $("." + a).addClass("active");
    return (
        <React.Fragment>
            <div id='spinner' className='show bg-dark position-fixed translate-middle w-100 vh-100 top-50 start-50 d-flex align-items-center justify-content-center'>
                <div className='spinner-grow text-primary' role='status'></div>
            </div>
            <div className='container-fluid fixed-top ft px-0 wow fadeIn bg-dark' data-wow-delay='0.1s'>
                <nav className='navbar navbar-expand-lg navbar-dark py-lg-0 px-lg-5 wow fadeIn' data-wow-delay='0.1s'>
                    <a href='/' className='navbar-brand ms-4 ms-lg-0'>
                        <h1 className='fw-bold text-primary m-0'>{appInfor.name1}<span className='text-white'>{appInfor.name2}</span></h1>
                    </a>
                    <button type='button' className='navbar-toggler me-4' data-bs-toggle='collapse' data-bs-target='#navbarCollapse'>
                        <span className='navbar-toggler-icon'></span>
                    </button>
                    <div className='collapse navbar-collapse' id='navbarCollapse'>
                        <div className='navbar-nav ms-auto p-4 p-lg-0  bg-dark'>
                            <a href='/' className='nav-item nav-link Home'>Home</a>
                            <a href='/about' className='nav-item nav-link About'>About</a>
                            <a href='/CV-Template' className='nav-item nav-link CV'>CV</a>
                            <div className='nav-item dropdown'>
                                <a href='#' className='nav-item nav-link User dropdown-toggle' data-bs-toggle='dropdown'>{u.text != '' ? u.text : 'Sign-In'}</a>
                                {u.body}
                            </div>
                        </div>
                    </div>
                </nav>
            </div>
        </React.Fragment>
    );
}

export default Nav;
