import React, { useState, useEffect } from "react";
import axios from 'axios';
function AppInfor(GetDataAppInfor) {
  const [dataAppFetch, setDataAppFetch] = useState([]);
  useEffect(() => {
    axios.get('https://cvprofile-author-dev-default-rtdb.asia-southeast1.firebasedatabase.app/appInfor/-NA5BPPIcv9UkPz7cjkA.json')
      .then(res => {
        setDataAppFetch(res.data)
        console.log(res.data);
        return res.data;
      })
      .catch(err => {
        console.log(err)
      })
  }, []);
  GetDataAppInfor(dataAppFetch);
}
export default AppInfor;